body {
    margin: 0;
    font-family: Arial, sans-serif;
}
.Wrapper{
    display: flex;
    flex-direction: column;
    /*border: 1px solid #ddd;*/
    flex:1;
    margin-right: 30px;
    margin-left: -45px;
    /*align-items: center;*/
}
.container {
    display: flex;
    width: 85%;
    margin: 0 auto;
}
.cv {
    text-decoration: none;
    font-size: 25px;
    color: rgba(15, 15, 15, 0.97);
}
.second_row{
    display: flex;
    align-items: flex-start;
    width: 100%;
    margin: 10px auto;
    padding: 10px 0;
    justify-content: space-between;
}
.blue-text{
    color:darkblue;
    font-weight: bold;
}
.News{
    width: 120%;
    text-align: left;
    margin-top: 50px;
}
.icon-links{
    display: flex;
    justify-content: center;
    gap: 20px; /* 调整元素之间的间隔 */
    color: black;
}
.photo {
    width: 70%;
    padding: 60px;

}
.fa-2x{
    color:black;
}
.photo img {
    max-width: 220px;
    border-radius: 50%;
    box-shadow: 0 0 10px rgba(0, 0, 0, 0.2);
}
.intro {
    width: 65%;
    padding: 10px;
    text-align: left;
    /*border: 1px solid #ddd;*/
    flex:2;
}
.intro h1 {
    margin-top: 0;
}
.projects-container{
    display: flex;
    flex-wrap: wrap;
    justify-content: space-between;
    text-align: center;
}
.projects-container img {
    width:328px;
    height:219px;
    margin-bottom: 10px;
}
.projects-container a:hover {
    font-weight: bold;
}
.whole{
    font-family: Gill Sans MT, serif;
}
@media (max-width: 768px) {
    .container {
        flex-direction: column;
    }
    .photo,
    .intro {
        flex: none;
        width: 80%;
    }
}
